@charset "UTF-8";
body, header, h1, h2, h3, h4, h5, h6, div, main, p, button, a, li, ul, ol, div, nav, table, td, th, tbody, thead, input {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #F46569;
  font-family: "HannaPro", sans-serif;
  border-radius: 0; }

button:focus {
  outline: none; }

body {
  background: #BEE5FB; }

*::-webkit-scrollbar {
  width: 1px; }

p {
  word-break: break-all; }

header, h1, h2, h3, h4, h5, h6, main {
  word-break: break-word; }

@font-face {
  font-family: "HannaPro";
  font-style: normal;
  src: url("../utils/font/BMHANNAPro.eot");
  src: url("../utils/font/BMHANNAPro.eot?#iefix") format("embedded-opentype"), url("../utils/font/BMHANNAPro.woff") format("woff"), url("../utils/font/BMHANNAPro.ttf") format("truetype");
  src: local(※), url("../utils/font/BMHANNAPro.woff") format("woff"); }

.watch-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 0 auto;
  width: 60%; }
  @media (min-width: 1025px) {
    .watch-container {
      width: 60%; } }
  @media (min-width: 701px) and (max-width: 1024px) {
    .watch-container {
      width: 60%; } }
  @media (max-width: 700px) {
    .watch-container {
      width: 100%;
      padding-right: 1rem;
      padding-left: 1rem; } }
  .watch-container .watch-mention {
    margin-bottom: 3rem;
    font-size: 2rem; }
    @media (max-width: 700px) {
      .watch-container .watch-mention {
        font-size: 1.33333rem; } }
  .watch-container section {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%; }
    .watch-container section .watch-info .watch-date {
      display: inline-block;
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      font-size: 1.5rem;
      margin-bottom: 1rem;
      font-family: 'Space Mono'; }
      .watch-container section .watch-info .watch-date .date-container {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0); }
        .watch-container section .watch-info .watch-date .date-container .datepicker {
          width: 100%;
          height: 100%;
          background: rgba(0, 0, 0, 0);
          color: rgba(0, 0, 0, 0);
          border: none;
          outline: none;
          cursor: pointer; }
    .watch-container section .watch-info .watch-subject {
      text-align: center;
      font-size: 1.5rem;
      margin-bottom: 1rem; }
    .watch-container section .watch-time {
      width: 100%;
      margin-bottom: 2rem;
      text-align: center; }
      .watch-container section .watch-time .watch-time-string {
        display: inline-block;
        font-family: 'Space Mono', monospace;
        font-size: 2rem;
        cursor: pointer; }
    .watch-container section .watch-control {
      width: 100%;
      display: flex;
      justify-content: center; }
      .watch-container section .watch-control button {
        width: 4rem;
        height: 4rem;
        border: none;
        outline: none;
        background: #F46569;
        color: #BEE5FB;
        border-radius: 50%;
        font-family: 'Space Mono';
        cursor: pointer; }
      .watch-container section .watch-control .second-item {
        margin-left: 3rem; }
      .watch-container section .watch-control #watch-on-stop {
        display: flex;
        justify-content: center; }
      .watch-container section .watch-control #watch-on-progress {
        display: flex;
        justify-content: space-around; }
