@charset "UTF-8";
body, header, h1, h2, h3, h4, h5, h6, div, main, p, button, a, li, ul, ol, div, nav, table, td, th, tbody, thead, input {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #F46569;
  font-family: "HannaPro", sans-serif;
  border-radius: 0; }

button:focus {
  outline: none; }

body {
  background: #BEE5FB; }

*::-webkit-scrollbar {
  width: 1px; }

p {
  word-break: break-all; }

header, h1, h2, h3, h4, h5, h6, main {
  word-break: break-word; }

@font-face {
  font-family: "HannaPro";
  font-style: normal;
  src: url("../utils/font/BMHANNAPro.eot");
  src: url("../utils/font/BMHANNAPro.eot?#iefix") format("embedded-opentype"), url("../utils/font/BMHANNAPro.woff") format("woff"), url("../utils/font/BMHANNAPro.ttf") format("truetype");
  src: local(※), url("../utils/font/BMHANNAPro.woff") format("woff"); }

.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 7rem;
  background: #F46569;
  z-index: 5; }
  @media (max-width: 700px) {
    .footer-container {
      height: 5rem; } }
  .footer-container .footer-copyright {
    color: #BEE5FB;
    font-family: 'Space Mono', sans-serif;
    font-size: 1rem;
    margin-left: 1rem; }
    @media (max-width: 700px) {
      .footer-container .footer-copyright {
        font-size: 0.66667rem; } }
    @media (max-width: 700px) {
      .footer-container .footer-copyright {
        margin-left: 0.5rem; } }
  .footer-container .footer-sns {
    margin-right: 1rem; }
    @media (max-width: 700px) {
      .footer-container .footer-sns {
        margin-right: 0.5rem; } }
    .footer-container .footer-sns ul {
      display: flex;
      list-style: none; }
      .footer-container .footer-sns ul li {
        color: #BEE5FB;
        margin-right: 1rem; }
        @media (max-width: 700px) {
          .footer-container .footer-sns ul li {
            margin-right: 0.5rem; } }
        .footer-container .footer-sns ul li img {
          display: block;
          width: 3rem; }
          @media (max-width: 700px) {
            .footer-container .footer-sns ul li img {
              width: 2rem; } }
