@charset "UTF-8";
body, header, h1, h2, h3, h4, h5, h6, div, main, p, button, a, li, ul, ol, div, nav, table, td, th, tbody, thead, input {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #F46569;
  font-family: "HannaPro", sans-serif;
  border-radius: 0; }

button:focus {
  outline: none; }

body {
  background: #BEE5FB; }

*::-webkit-scrollbar {
  width: 1px; }

p {
  word-break: break-all; }

header, h1, h2, h3, h4, h5, h6, main {
  word-break: break-word; }

@font-face {
