@charset "UTF-8";
body, header, h1, h2, h3, h4, h5, h6, div, main, p, button, a, li, ul, ol, div, nav, table, td, th, tbody, thead, input {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #F46569;
  font-family: "HannaPro", sans-serif;
  border-radius: 0; }

button:focus {
  outline: none; }

body {
  background: #BEE5FB; }

*::-webkit-scrollbar {
  width: 1px; }

p {
  word-break: break-all; }

header, h1, h2, h3, h4, h5, h6, main {
  word-break: break-word; }

@font-face {
  font-family: "HannaPro";
  font-style: normal;
  src: url("../utils/font/BMHANNAPro.eot");
  src: url("../utils/font/BMHANNAPro.eot?#iefix") format("embedded-opentype"), url("../utils/font/BMHANNAPro.woff") format("woff"), url("../utils/font/BMHANNAPro.ttf") format("truetype");
  src: local(※), url("../utils/font/BMHANNAPro.woff") format("woff"); }

.main-container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1; }
  .main-container img {
    display: inline-block;
    position: absolute;
    opacity: 1; }
    @media (orientation: portrait) {
      .main-container img {
        height: 100vh;
        transform: translateX(calc(50vw - 50%)); } }
    @media (orientation: landscape) {
      .main-container img {
        width: 100vw;
        transform: translateY(calc(50vh - 50%)); } }
  .main-container .main-mention {
    position: fixed;
    bottom: 20%;
    left: 50%;
    transform: translateX(-50%);
    color: #BEE5FB;
    font-size: 1rem;
    font-weight: 300;
    text-align: center; }
